.Aboutme {
    width: 70%;
    margin: auto;
    margin-top: 12rem;
    margin-bottom: 2rem;
}
.Aboutme p  {
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}


@media (min-width: 481px) and (max-width: 767px) {

    .Aboutme {
        width: 100%;
        margin: auto;
        margin-top: 2rem;
        margin-bottom: 2rem;
    }
  
}

@media (max-width: 480px) {
   .Aboutme {
    width: 100%;
    margin: auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
}
    
}


