nav {
    background-color: transparent;
    font-family: sans-serif;
    position: fixed;
    /* padding: 10px 0px 10px 0px; */
    transition: all .6s ease;
    user-select: none;
    width: 100%;
    z-index: 999;
    
}


nav .max-width {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 1rem 0px 1rem;

}

.navlink-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    list-style-type: none;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    
    
}
.navlink-list li {
    text-transform: uppercase;
}


.navbar-sticky {
    background-color: #fff;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.name-logo {
    width: 22%;;
}

.name-logo img {
    width: 100%;
}
.menu-btn {
    display: none;
}

.menu-btn-active {
    width: 100%;
    display: flex;

}
.menuList {
    display: none;
}



@media (min-width: 768px) and (max-width: 1024px){
    .menu-btn {
        display: block;
    }
    .navlink-list {
        display: none;
    }
    .name-logo {
        width: 30%;;
    }

    nav {
        background-color: #fff;
    }

    .menuList {
        display: block;
    }
    
    nav .max-width {
        display: none

    }

    nav {
        background-color: #fff;
        
    }
    
    
}
@media (min-width: 481px) and (max-width: 767px) {
    .menu-btn {
        display: block;
    }
    .navlink-list {
        display: none;
    }
    .name-logo {
        width: 40%;;
    }
    nav .max-width {
        display: none
    
    }

    .menuList {
        display: block;
    }
    nav {
        background-color: #fff;
    }
    
   
}

@media (max-width: 480px) {
    .menu-btn {
        display: block;
    }
    .navlink-list {
        display: none;
    }
    .name-logo {
        width: 50%;;
    }
    nav .max-width {
        display: none
    
    }

    .menuList {
        display: block;
    }
    nav {
        background-color: #fff;
    }
   
    
}


