.border-img {
    width: 300px;
    height: 300px;
    background-color: gray;
    border-radius: 40% 60% 78% 22% / 63% 76% 24% 37% ;
    margin-top: 10rem;
    position: absolute;
    left: 11rem;
    top: 2rem;
    z-index: 2;

}
.border-img2 {
    width: 350px;
    height: 350px;
    background-color: rgb(114, 103, 103);
    border-radius: 28% 72% 52% 48% / 52% 29% 71% 48% ;
    margin-top: 10rem;
    position: absolute;
    left: 10rem;
    top: -1rem;
   

}


.home-page {
    display: flex;
    width: 100%;
}

.intro {
width: 100%;
 margin-top: 10rem;
 margin-left: 30rem;
 font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}




.border-img img {
    width: 80%;
    position: relative;
    bottom: 5rem;
    border-radius: 28% 72% 52% 48% / 52% 29% 71% 48% ;
}


